var Blip = /** @class */ (function () {
    function Blip() {
    }
    Blip.fromXML = function (word, element) {
        var blip = new Blip();
        // 目前值支持 embed 这一种
        var embedId = element.getAttribute('r:embed') || '';
        var rel = word.getDocumentRels(embedId);
        if (rel) {
            blip.embled = rel;
            blip.src = word.loadImage(blip.embled);
        }
        return blip;
    };
    return Blip;
}());

export { Blip };
